import api from '../../common/request'
import { debounce, insertArray } from '../../common/util'

const SEARCH_KEY = '__search__'
const SEARCH_MAX_LENGTH = 6

export default {
	data() {
		return {
			query: '',
			scrollHeight: 0,
			hotlist: [],
			resultlist: [],
			page: 1,
			limit: 10,
			count: '',
			loading: true,
			historylist: []
		}
	},
	onLoad() {
		this.historylist = uni.getStorageSync(SEARCH_KEY) || []
		this.getHotListData(1)
		this.$watch('query', debounce((newQuery) => {
			if (!newQuery) {
			} else {
				// setTimeout(() => {
					this.resultlist = []
					this.page = 1
					this.getResultListData()
				// }, 20)
			}
		}, 200))
	},
	onReady() {
		const windowHeight = wx.getSystemInfoSync().windowHeight
		this.scrollHeight = (windowHeight - 65) * 2
	},
	onShow() {
		// this.$watch('query', debounce((newQuery) => {
		//     if (!newQuery) {
		// 			
		//     } else {
		//       // setTimeout(() => {
		//         this.resultlist = []
		//         this.page = 1
		//         this.getResultListData()
		//       // }, 20)
		//     }
		//   }, 200))
	},
	methods: {
		// 字数转换
		wordsCount(num) {
			if (num > 10000) {
			  return Math.round(num / 10000) + '万字'
			} else {
			  return num + '字'
			}
		},
		// queryInput(e) {
		//   let newQuery = e.detail.value
		//   let oldQuery = this.data.query
		//   this.setData({
		//     query: newQuery,
		//   })
		//   if (newQuery !== oldQuery) {
		//     this.setData({
		//       resultlist: [],
		//       page: 1
		//     })
		//     this.getResultListData()
		//     // util.debounce(this.getResultListData(), 200)
		//   }
		// },
		clearQuery() {
			this.query = ''
		  this.resultlist = []
		  this.page = 1
		},
		exchangeFuncBtn() {
		  let num = Math.floor(Math.random()*(90 - 1) + 1)
		  this.getHotListData(num)
		},
		queryHotBtn(e) {
		  this.query = e.currentTarget.dataset.title
		  this.getResultListData()
		},
		scrollMoreList() {
		  let length = this.resultlist.length
		  if (length >= this.count) {
				this.loading = false
		  } else {
		    this.page++
		    this.getResultListData()
		  }
		},
		// 选中书籍跳转
		selectItem(e) {
		  let item = e.currentTarget.dataset.item
		  uni.navigateTo({
		    url: `../book/book?id=${item.book_id}`,
		    success: () => {
		      this.saveSearch(this.query)
		    }
		  })
		},
		saveSearch(query) {
		  let searches = this.historylist
		  insertArray(searches, query, item => {
		    return item === query
		  }, SEARCH_MAX_LENGTH)
		  uni.setStorageSync(SEARCH_KEY, searches)
		  this.historylist = searches
		},
		clearHistoryBtn() {
		  uni.removeStorageSync(SEARCH_KEY)
		  this.historylist = []
		},
		getHotListData(page) {
		  api.get('/index/searchHot', {
		    page
		  }).then(res => {
		    if (res.code === 200) {
					this.hotlist = res.data
		    }
		  })
		},
		getResultListData() {
		  let para = {
		    keywords: this.query,
		    page: this.page,
		    limit: this.limit
		  }
		  api.post('/index/searchResult', para).then(res => {
		    if (res.code === 200) {
		      let status = true
		      let arr = this.resultlist
		      arr = arr.concat(res.data)
		      if (res.extra.count < this.limit) {
		        status = false
		      } else {
		        status = true
		      }
					this.resultlist = arr
		      this.count = res.extra.count
		      this.loading = status
		    } else {
		      if (this.page === 1) {
						this.resultlist = []
		        this.count = 0
		        this.loading = false
		      }
		    }
		  })
		}
		
	}
}	
